<template>
    <ChartBar :cdata="cdata" />
</template>

<script>
import ChartBar from './chartBar.vue'
export default {
    data() {
        return {
            cdata: {
                height: '520px',
                width: '800px',
                colors: ['#4D73BE', '#F2F754'],
                legendData: ['应收余额', '应付余额'],
                xAxisData: ['工商银行', '招商银行', '光大银行', '建设银行', '民生银行', '中国银行', '浦发银行'],
                yAxisData: [
                    {
                        type: 'value',
                        name: '应收余额',
                        position: 'right',
                        alignTicks: true,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#4D73BE',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                    },
                    {
                        type: 'value',
                        name: '应付余额',
                        position: 'left',
                        alignTicks: true,
                        axisLine: {
                            show: true,
                            lineStyle: {
                                color: '#F2F754',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}',
                        },
                    },
                ],
                seriesData: [
                    {
                        name: '应收余额',
                        type: 'bar',
                        barWidth : 30,
                        data: [2.0, 4.9, 7.0, 23.2, 11, 13, 9],
                    },
                    {
                        name: '应付余额',
                        type: 'line',
                        data: [2.0, 2.2, 3.3, 4.5,9 , 20, 13, 11],
                    },
                ],
            },
        }
    },
    components: {
        ChartBar,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 8) - 1;
                let tmp2 = Math.ceil(Math.random() * 8) - 1;
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 0.5;
                    cdata.seriesData[0].data[tmp1] = num;

                    let num2 = cdata.seriesData[1].data[tmp2]
                    num2 = num2 + 0.5;
                    cdata.seriesData[1].data[tmp2] = num2;
                    this.cdata = cdata;
                }
            }, 1000)
        },
    },
}
</script>

