<template>
    <div>
        <Echart :options="options" :height="cdata.height" :width="cdata.width"></Echart>
    </div>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler() {
                this.options = {
                    color: this.cdata.colors,
                    legend: {
                        top: '20',
                        data: this.cdata.legendData,
                    },
                    radar: {
                        // shape: 'circle',
                        indicator: this.cdata.indicatorData,
                        radius: 180,
                    },
                    series: [
                        {
                            name: 'Budget vs spending',
                            type: 'radar',
                            areaStyle: {},
                            data: this.cdata.seriesData,
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

<style lang="scss" scoped></style>
