<template>
    <ChartRadar :cdata="cdata" />
</template>

<script>
import ChartRadar from './chartRadar.vue'
export default {
    data() {
        return {
            cdata: {
                height: '620px',
                width: '800px',
                colors: ['#4D73BE', '#439479'],
                legendData: ['应收票据余额', '应付票据余额'],
                indicatorData: [
                    { value: 100, name: '工商银行' },
                    { value: 30, name: '招商银行' },
                    { value: 20, name: '光大银行' },
                    { value: 5, name: '建设银行' },
                    { value: 60, name: '民生银行' },
                    { value: 80, name: '中国银行' },
                ],
                seriesData: [
                    {
                        value: [4200, 3000, 20000, 35000, 50000, 18000],
                        name: '应收票据余额',
                    },
                    {
                        value: [5000, 5000, 5000, 26000, 42000, 5000],
                        name: '应付票据余额',
                    },
                ],
            },
        }
    },
    components: {
        ChartRadar,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 6) - 1;
                let tmp2 = Math.ceil(Math.random() * 6) - 1;
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata));
                    let num = cdata.seriesData[0].value[tmp1]
                    num = num + 500;
                    cdata.seriesData[0].value[tmp1] = num;

                    let num2 = cdata.seriesData[1].value[tmp2]
                    num2 = num2 + 500;
                    cdata.seriesData[1].value[tmp2] = num2;
                    this.cdata = cdata;
                }
            }, 2000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
